import { html } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import { createEvent } from '../other/event';
import { isVisible } from '../directives/isVisible';
function emitValueDefault(e, value) {
    const target = e.target;
    const detail = { value, checked: target.checked };
    const changeEvent = createEvent(e.type, { detail });
    target.parentElement?.dispatchEvent(changeEvent);
}
export const radioHtmlTemplate = ({ name, value, text, emitValue = emitValueDefault, disabled = false, error = '' }) => html `
        <div class="container">
            <label class=${classMap({ disabled, error })}>
                <input
                        class=${classMap({ disabled, error })}
                        type="radio" .name=${name} .value=${value}
                        @click=${(e) => e.stopPropagation()}
                        @change=${(e) => {
    e.stopPropagation();
    emitValue(e, value);
}}>
                ${isVisible(!!text, html `<span class="body-3">${text}</span>`)}
            </label>
            ${isVisible(!!error, html `<span class=${classMap({ 'error-text': true })}>${error}</span>`)}
        </div>
    `;
