import { css, unsafeCSS } from 'lit';
import { baseCSS } from '../utils/css/css-utils';
import { BaseStyleType, IconStyleType } from './config/constants';
export const stylesBaseButton = [
    baseCSS,
    css `
        :host {
            --base-padding: 0
        }

        button {
            display: flex;
            justify-content: center;
            align-items: center;
            background: var(--blue-120);
            color: var(--common-white);
            border: none;
            outline: none;
            border-radius: var(--border-radius-4);
            cursor: pointer;
            transition-duration: .25s;
            transition-property: background-color, border-color, box-shadow;
            transition-timing-function: ease;
        }

        button:focus {
            box-shadow: 0 0 0 calc(var(--base-size)/2) var(--blue-190);
        }

        button[style-type=${unsafeCSS(BaseStyleType.DEFAULT)}]:not(:disabled):hover {
            background: var(--blue-100);
        }

        button[style-type=${unsafeCSS(BaseStyleType.DEFAULT)}]:not(:disabled):active {
            background: var(--blue-90);
        }

        button[style-type=${unsafeCSS(BaseStyleType.DEFAULT)}]:disabled {
            background: var(--grey-20);
            color: var(--grey-100);
        }

        button[style-type=${unsafeCSS(BaseStyleType.OUTLINE)}] {
            --border: calc(var(--space-4)/4);
            background: transparent;
            border: var(--border) solid var(--grey-30);
            padding: 0 calc(var(--base-padding) - var(--border));
            color: var(--blue-120)
        }

        button[style-type=${unsafeCSS(BaseStyleType.OUTLINE)}]:hover {
            border-color: var(--blue-120);
        }

        button[style-type=${unsafeCSS(BaseStyleType.OUTLINE)}]:not(:disabled):active {
            border-color: var(--blue-100);
        }

        button[style-type=${unsafeCSS(BaseStyleType.OUTLINE)}]:not(:disabled):focus {
            border-color: var(--grey-30);
        }

        button[style-type=${unsafeCSS(BaseStyleType.OUTLINE)}]:disabled {
            color: var(--grey-80);
            border-color: var(--grey-30);
        }

        button[style-type=${unsafeCSS(BaseStyleType.FLAT)}] {
            background: transparent;
            color: var(--blue-120)
        }

        button[style-type=${unsafeCSS(BaseStyleType.FLAT)}]:not(:disabled):hover,
        button[style-type=${unsafeCSS(BaseStyleType.FLAT)}]:not(:disabled):active {
            background: var(--blue-10);
        }

        button[style-type=${unsafeCSS(BaseStyleType.FLAT)}]:disabled {
            color: var(--grey-80);
        }
    `
];
export const stylesButton = [
    stylesBaseButton,
    css `
    :host {
        --content-gap: 0;
        --icon-size: 0;
    }

    .button__icon {
        height: var(--icon-size);
        width: var(--icon-size);
    }

    .button__content {
        display: flex;
        align-items: center;
        gap: var(--content-gap);
    }
    `
];
export const stylesIconButton = [
    stylesButton,
    css `
    button:focus {
        border-color: var(--blue-180); //TODO: другой цвет обводки в макете для Round
    }

    button[style-type=${unsafeCSS(IconStyleType.ROUND)}] {
        border-radius: 100%;
        background-color: var(--green-120);
        padding: var(--base-padding);
        height: fit-content;
    }

    button[style-type=${unsafeCSS(IconStyleType.ROUND)}]:hover {
        background-color: var(--green-100)
    }

    button[style-type=${unsafeCSS(IconStyleType.ROUND)}]:active {
        background-color: var(--green-100)
    }

    button[style-type=${unsafeCSS(IconStyleType.ROUND)}]:disabled {
        background-color: var(--grey-50)
    }
    `
];
